<template>
  <div>
    <nav-bar>
      <slot>学习资料</slot>
    </nav-bar>
    <div class="search">
      <input type="text" placeholder="搜索" />
    </div>
    <ul class="mater">
      <li><router-link to="/special">专题</router-link></li>
      <li><router-link to="/curr">课程</router-link></li>
      <li><router-link to="/books">图书</router-link></li>
      <li><router-link to="/periodical">期刊</router-link></li>
      <li><router-link to="/lecture">讲座</router-link></li>
    </ul>
    <div>
      <router-view>
      </router-view>
    </div>

  </div>
</template>

<script>
   import $ from 'jquery'
  $(function(){
    $(".mater li a").on("click",function(){
      // alert(1)
      $(this).parent().addClass("active");
      $(this).parent().siblings().removeClass("active");
    })
  })
</script>

<style scoped>
  .search{
    text-align: center;
  }
  .search input{
    width: 96%;
    height: 5rem;
    margin-top: 2rem;
    font-size: 2.5rem;
    text-align: center;
    outline: none;
    border: 0;
    background-color: #eee;
    border-radius: 8rem;

  }
  .mater{
    width: 100%;
    height: 10rem;
    border-bottom: 1.5rem solid #f4f4f4;
  }
  .mater li{
    list-style-type: none;
    width: 20%;
    height: 8rem;
    line-height: 8rem;
    float: left;
    text-align: center;
  }
  .mater .router-link-active{
    color: blue;
    border-bottom: 0.5rem solid blue;
  }
</style>



