<template>
  <div id="footer">
    <ul class="tabbar">
      <li class="active"><router-link to="/home">
        <p class="iconfont">&#xe8e0;</p>
        <!-- <p class="iconfont">&#xe918;</p> -->
        <span>首页</span>
      </router-link></li>
      <li><router-link to="/news">
        <p class="iconfont">&#xe8ed;</p>
        <!-- <p class="iconfont">&#xe912;</p> -->
        <span>消息</span>
      </router-link></li>
      <li><router-link to="/note">
        <p class="iconfont">&#xe8ac;</p>
        <!-- <p class="iconfont">&#xe929;</p> -->
        <span>笔记</span>
      </router-link></li>
      <li><router-link to="/me">
        <p class="iconfont">&#xe8ef;</p>
        <span>我的</span>
      </router-link></li>
    </ul>
  </div>
</template>

<script>
  import $ from 'jquery'
  $(function(){
    $(".tabbar li").on("click",function(){
      $(this).addClass("active").siblings().removeClass("active");
    })
  })
</script>

<style scoped>
  .tabbar li{
    list-style-type: none;
    float: left;
    /* font-size: 3.2rem; */
    color: wheat;
    height: 100%;
    width: 25%;
    text-align: center;
    /* line-height: 8rem; */
    /* margin-bottom: 0.8rem; */
    /* background-color: #2EA372; */
  }
  .tabbar a{
    text-decoration: none;
    color: #555;
  }
  .tabbar p{
    font-size: 5rem;
    margin: 0;
  }
  .tabbar span{
    font-size: 1.5rem;
  }
  .tabbar .active a{
    color: blue;
  }
</style>



