    "content":"<p>百年的变迁中，未曾改变的是情感。四代人走过的路程，都被中国人的情感之美和人情之美浸润过，彰显着血浓于水、家寓于国的人间至理。这部情感作品对“亲情友情爱情”还有“故乡和远方”等乡愁里的核心命题做了深刻动情的描绘，道尽了人何以回家，何以乡愁，何以不舍不弃家国乡土乡情乡音的根由。</p><p>我只会在写信的时候，在爸爸妈妈的称呼前加上“亲爱的”。生活中我们也是这么亲密，他们也是我的亲爱的爸爸妈妈，可我几乎没告诉过他们我爱他们，我也不说“谢谢”和“对不起”，我怕这些用词疏远了我们的关系。我们那么亲近，我们从不计较对方说了什么，我们懂得感恩，我们也可以原谅一切。可是很多时候，我想说声“我爱你”，说声“谢谢”，说声“对不起”。</p>",
