  import BannerView from '../common/Banner.vue'
  export default{
    name:'microapplication',
    data(){
      return{
        microapplication:[
  {"title":"推荐",
    "recommend":[
    { "title":"考试","icon":"icon-bianji"},
    { "title":"小组广场","icon":"icon-Customermanagement-fill "},
    { "title":"培训课程","icon":"icon-flag"},
    { "title":"尔雅咨询","icon":"icon-wenda "},
    { "title":"直播广场","icon":"icon-jiju "},
    { "title":"学习档案","icon":"icon-wenjuan "},
    { "title":"课程广场","icon":"icon-ditu "},
    { "title":"活动","icon":"icon-filter"},
    { "title":"星阅读","icon":"icon-collection-fill "}
  ]},
