  ]}

]

      }
    },
    components:{
      BannerView
    },
    created() {
      let url = 'src/data/microapplication.data';
      console.log(url)
      // this.$http.get("src/data/courseList.data")
      this.$http.get(url)
      .then(res=>{
        console.log(res)
        this.microapplication=res.data
      })
      .catch(function(err){
        console.log(err)
      })
    }
  }
</script>

<style scoped>
  .microapplication h4{
    width: 100%;
    height: 6rem;
    line-height: 6rem;
    background-color: #f4f4f4;
    padding-left: 2rem;
  }
  .microapplication ul li{
    list-style-type: none;
    width: 25%;
    padding: 0rem 0 2rem;
    text-align: center;
    /* background-color: #678; */
    float: left;
  }
  .microapplication .icon{
    font-size: 8rem;
  }
  .clear{
    clear: both;
  }
  .green .icon{color: #2f8836;}
  .blue .icon{color: #2f4388;}
  .yellow .icon{color: #888529;}
  .red .icon{color: #8b0045;}
</style>



