<template>
<div id="courseDetail">
  <nav-bar >
    <slot>推荐详情</slot>
  </nav-bar>
  <div class="top"></div>
  <div v-for="(item,index) in recomDEtail">
    <div v-if="item.id==id" class="recomDEtail">
      <h3>{{item.title}}</h3>
      <img :src="item.src"/>
      <p v-html="item.content">item.content</p>
    </div>
  </div>
</div>
</template>

<script>
   export default {
    data(){
      return {
        title: '新闻列表',
        id:0,
        recomDEtail:[
  {
    "id":1,
    "title":"必读美文：章珺《回家·四代人的老照片》之亲爱的爸爸妈妈",
    "content":"<p>百年的变迁中，未曾改变的是情感。四代人走过的路程，都被中国人的情感之美和人情之美浸润过，彰显着血浓于水、家寓于国的人间至理。这部情感作品对“亲情友情爱情”还有“故乡和远方”等乡愁里的核心命题做了深刻动情的描绘，道尽了人何以回家，何以乡愁，何以不舍不弃家国乡土乡情乡音的根由。</p><p>我只会在写信的时候，在爸爸妈妈的称呼前加上“亲爱的”。生活中我们也是这么亲密，他们也是我的亲爱的爸爸妈妈，可我几乎没告诉过他们我爱他们，我也不说“谢谢”和“对不起”，我怕这些用词疏远了我们的关系。我们那么亲近，我们从不计较对方说了什么，我们懂得感恩，我们也可以原谅一切。可是很多时候，我想说声“我爱你”，说声“谢谢”，说声“对不起”。</p>",
    "src":"src/assets/img/tui1.jpg"
  },
  {
    "id":2,
    "title":"曾国藩江头人生的八大金句",
    "content":"<li>士人第一要有志，第二要有识，第三要有恒。有志则不甘为下流，有识则知学问无尽，不敢以一得自足，有恒则断无不成之事，三者缺一不可。</li><li>谁人可慢？何事可弛？弛事者无成，慢人者反尔。</li><li>用功譬若掘井，与其多掘数井而皆不及泉，何若老守一井，力求及泉而用之不竭乎？</li><li>军中阅历有年，益知天下事当于大处着眼，小处下手。陆氏但称‘先立乎其大者’，若不辅以朱子‘铢积寸累’工夫，则下梢全无把握。</li><li>凡道理不可说得太高，太高则近于矫，近于伪。吾与僚友相勉，但求其不晏起、不撒谎二事，虽最浅近而已大有益于身心矣。</li><li>大抵任事之人，断不能有誉而无毁，有恩而无怨。自修者但求大闲不逾，不可因讥议而馁沈毅之气。衡人者但求一长可取，不可因微瑕而弃有用之材。苟于峣峣者过事刻求，则庸庸者反得幸全。</li><li>养身之道，以君逸臣劳四字为要。</li><li>凡文有气则有势,有识则有度,有情则有韵,有趣则有味</li>",
    "src":"src/assets/img/ia_100000000506.jpg"
  }
  ,
  {
    "id":3,
    "title":"做一只有线的风筝",
    "content":"<p>摔了一跤，好想说不严重，因为以前也受过很多类似的伤，过一两个星期就好了。 可又觉得挺严重，我迫不及待地想快点离开这些熟悉的地方，去的越远越好，我不想待在家的附近。 对于我来说，家是远方的牵挂，但又是远方的羁绊。 每次长途旅行的初期，总是最难的，你要与现有舒适的环境拜别，你昨天或前天还躺在空调卧室，等老妈第十次喊你起床吃早餐，而今天，你却不知会在何处停留，晚上又会住在何处。 这种心理落差在初期是特别明显的，但每次旅行又喜欢选择从家乡出发，因为一直觉得，唯有从哪里出发，我们才能懂得如何回到哪里，有一天，路上的行人问我：你要回到哪里去啊？ 我答：我从哪里来，就回到哪里去，那里是我的故土，有我的家人，朋友，以及我过往的岁月和梦想。 </p><p>我知道，这种羁绊感越强，代表我对家的归属感也就越强。 但我还是要挣脱羁绊，去追随更宽广的自由，可是，我也知道，无论我怎么挣扎，都是无法挣脱羁绊的那条线，当然，我也不想挣脱，我只想化成一只风筝，被家牵着，在蓝天白云间翱翔，尽可能让拉线的家人把线放长一些，好让我俯瞰整个世界。 待到夜幕降临的时候，老妈会把我收回去吃晚饭。 </p><p>已经好久没受过伤，膝盖上有之前好几个伤疤，小时候与中学时期踢球摔的。 以前受伤总会与家人说，因为每次受伤都能得到父母无限的关爱，以至于我一直觉得，是不是我受伤太严重了，会不会残疾或留下后遗症，赶紧告诉父母，让他们给我提前治疗，也许还有救。 后来随着成长，有些小伤小病也就自己搞定了。 现在发觉，当年父母与其他父母不同的一点就是，每次我受伤之后，他们不会过分责怪我调皮，只会随着年龄成长给我恰到其实的提醒，这样我就不会在每次受伤后隐瞒伤情，造成更大的后果。 他们让我自由飞翔，却又随时自愿承担接住跌落的我。 </p>",
    "src":"src/assets/img/ia_100000000490.jpg"
  }
]
,// 新闻列表数据
      }
    },
    mounted(){
      // this.getData()
    },
    created() {
      // getData(){
        // var _this=this
        // console.log(this.$route.params)
        // 1: 获取路由参数
        let { id } = this.$route.params;
        console.log(id)
        this.id=id;
        // 2: 拼接后台url发起请求
        let url = 'src/data/recommend.data';
        console.log(url)
        // this.$http.get("src/data/courseList.data")
        this.$http.get(url)
        .then(res=>{
          console.log(res)
          this.recomDEtail=res.data
        })
        .catch(function(err){
          console.log(err)
        })
      // }
    }
   }
</script>

<style scoped>
  .recomDEtail .icon{
    font-size: 4rem;
    color: #ccc;
  }
  .recomDEtail li{
    margin: 2rem 0;
    font-size: 2.8rem;
    height: 3rem;
    line-height: 3rem;
  }
  .recomDEtail img{
    width: 100%;
  }

  .recomDEtail p{
    font-size: 1.5rem;
    text-indent:4em;
  }
</style>



