      width:100%;
      /* height: 12rem; */
      float: left;
      padding: 2rem;
      /* background-color: #789; */
      border-bottom: 0.1rem solid #ddd;
    }
    .infoDetail h4{
      width: 10rem;
      float: left;
      font-weight: inherit;
      /* background-color: #678; */
    }
    .infoDetail p{
      width: 30rem;
      float: left;
      color: #666;
      /* background-color: #678; */
    }
    .infoDetail span{
      display: block;
      float: right;
      width: 3rem;
      font-size: 5rem;
      color: #999;
    }
  </style>



