<template>
  <div class="">
    <div class="top"></div>
    <div class="topbar">
      <!-- <router-link to="/home" class="iconfont">&#xe744;</router-link> -->
      <span @click="goback" class="iconfont">&#xe744;</span>
      <slot></slot>
      <!-- <p >{{title}}</p> -->
      <!-- <span class="iconfont">&#xe8d1;</span> -->
      <!-- <slot @click="goback"></slot> -->
    </div>

  </div>
</template>

<script>
   export default {
    name: 'nav-bar',
    props:['title'], // props属性再template中可以直接用
    created() {
        // 再这里就使用this.title
    },
    methods:{
        goback(){
            // alert(1)
            this.$router.go(-1);
        }
    },
    data(){
        return {

        }
    }
   }
</script>

<style scoped>
  .top{
    width: 100%;
    height: 8rem;
    position: relative;
    /* background-color: red; */
    top: 0;
  }
  .topbar{
    width: 100%;
    height: 8rem;
    line-height: 8rem;
    background-color: #fdfdfd;
    /* background-color: green; */
    border-bottom: 0.0625rem solid #ccc;
    position: relative;
    position: fixed;
    top: 0;
    text-align: center;
  }
  .topbar span{
    display: block;
    width: 8rem;
    height: 8rem;
    /* background-color: #467; */
    position: absolute;
    left: 1rem;
    top: 0;
    font-size: 5rem;
  }
</style>



