import Home from './components/Home.vue'
import Me from './components/Me.vue'
import News from './components/News.vue'
import Note from './components/Note.vue'

import DetailH1 from './components/components/Detail_h1.vue'
import CourseDetail from './components/components/CourseDetail.vue'
import RecomDetail from './components/components/RecomDetail.vue'
import Curriculum from './components/components/Curriculum.vue'
import InfoDetail from './components/components/InfoDetail.vue'
import Wait from './components/components/Wait.vue'
import Collection from './components/components/Collection.vue'
import CloudDisk from './components/components/CloudDisk.vue'
import Group from './components/components/Group.vue'
import Notebook from './components/components/Notebook.vue'
import Setup from './components/components/Setup.vue'

import Reply from './components/news/Reply.vue'
import Verification from './components/news/Verification.vue'
import Inbox  from './components/news/Inbox .vue'
import MailList from './components/news/MailList.vue'

import Microapplication from './components/components/Microapplication.vue'
import MaterialList from './components/materials/MaterialList.vue'

import Special from './components/materials/Special.vue'
import Curr from './components/materials/Curr.vue'
import Books from './components/materials/Books.vue'
import Periodical from './components/materials/Periodical.vue'
import Lecture from './components/materials/Lecture.vue'


export default{
  routes:[
    {path:"/home",component:Home},
    {path:"/me",component:Me},
    {path:"/news",component:News},
    {path:"/note",component:Note},
    {path:"/detailH1",component:DetailH1},
    {
      path:"/courseDetail/:id",
      name:'CourseDetail',
      component:CourseDetail,
    },
    {
      path:"/recomDetail/:id",
      name:'RecomDetail',
      component:RecomDetail,
    },
    {path:"/curriculum",component:Curriculum},
    {path:"/infoDetail",component:InfoDetail},
    {path:"/wait",component:Wait},
    {path:"/collection",component:Collection},
    {path:"/cloudDisk",component:CloudDisk},
    {path:"/group",component:Group},
    {path:"/notebook",component:Notebook},
    {path:"/setup",component:Setup},
    {path:"/verification",component:Verification},
    {path:"/reply",component:Reply},
    {path:"/inbox",component:Inbox},
    {path:"/mailList",component:MailList},
    {path:"/microapplication",component:Microapplication},

    {
      path:"/materialList",component:MaterialList,
      children:[
        {path:"/special",component:Special},
        {path:"/curr",component:Curr},
        {path:"/books",component:Books},
        {path:"/periodical",component:Periodical},
        {path:"/lecture",component:Lecture}
      ]
    },

    {path:"*",redirect:"/home"}
  ]
}



