<template>
  <div id="banner">
    <!-- <img src="../assets/img/logo.png" />
     -->
     <div class="swiper-container">
       <div class="swiper-wrapper banner_img">
         <!-- <div class="swiper-slide" data-swiper-autoplay="1000"><img src="../../assets/img/ia_100000000420.jpg"></div> -->
         <!-- <div class="swiper-slide" data-swiper-autoplay="1000"><img src="../../assets/img/ia_100000000421.jpg"></div> -->
         <!-- <div class="swiper-slide" data-swiper-autoplay="1000"><img src="../../assets/img/ia_100000000422.jpg"></div> -->
         <!-- <div class="swiper-slide" data-swiper-autoplay="1000"><img src="../../assets/img/k1.png"></div> -->
         <div class="swiper-slide" data-swiper-autoplay="1000"><img src="../../assets/img/k3.png"></div>
       </div>
     </div>
