<script>
  import Swiper from 'swiper'
  // import "swiper/dist/js/swiper.js"
  // import "swiper/dist/css/swiper.css"
  export default{
    mounted(){
      // var mySwiper = new Swiper('.swiper-container', {
      //   autoplay:true,//等同于以下设置
      //   // autoplay: {
      //   //   // delay: 5000,
      //   //   // stopOnLastSlide: false,
      //   //   // disableOnInteraction: true,
      //   //   },
      // });
      var mySwiper = new Swiper('.swiper-container', {
        autoplay: {
          delay: 2000,//1秒切换一次
        },
      });
    }
  }

</script>

<style>
  img{
    width: 100%;
  }
  .swiper-slide img{
    width: 100%;
    height: 30rem;
  }
</style>



