<template>
  <div id="news">
    <div class="topbar">
      <p>消息</p>
      <span class="iconfont">&#xe7b0;</span>
    </div>
    <div class="search">
      <input type="text" placeholder="搜索" />
    </div>
    <p class="tips">
      <span class="iconfont">&#xe8f7;</span>
      点击开启消息通知，不在错过重要消息
      <span class="iconfont close">&#xe74a;</span>
    </p>
    <ul class="uli">
      <li>
        <router-link to="/mailList">
        <span class="iconfont green">&#xe7aa;</span>
        <p>通讯录</p>
        </router-link>
      </li>
      <li>
        <router-link to="/inbox">
        <span class="iconfont blue">&#xe921;</span>
        <p>收件箱</p>
        </router-link>
      </li>
      <li>
        <router-link to="/reply">
        <span class="iconfont blue">&#xe7b5;</span>
        <p>回复我的</p>
        </router-link>
      </li>
      <li>
        <router-link to="/verification">
        <span class="iconfont green">&#xe78d;</span>
        <p>验证信息</p>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
   import $ from 'jquery'
  $(function(){
    $(".close").on("click",function(){
      $(this).parent().toggle()
    })
  })
</script>

<style scoped>

  .topbar{
    height: 8rem;
    line-height: 8rem;
    background-color: #fdfdfd;
    border-bottom: 0.0625rem solid #ccc;
    position: relative;
  }
  .topbar p{
    text-align: center;
  }
  .topbar span{
    width: 3.125rem;
    height: 3.125rem;
    position: absolute;
    right: 3rem;
    top: 0;
    font-size: 5rem;
  }
  .search{
    text-align: center;
  }
  .search input{
    width: 96%;
    height: 5rem;
    margin-top: 2rem;
    font-size: 2.5rem;
    text-align: center;
    outline: none;
    border: 0;
    background-color: #eee;
    border-radius: 8rem;

  }
  .tips{
    width: 100%;
    height: 5rem;
    line-height: 5rem;
    /* background-color: #003030; */
    font-size: 1.8rem;
    color: #888;
    padding-left: 1.875rem;
    letter-spacing: 0.5rem;
  }
  .tips span{
    font-size: 2.2rem;
    color: blue;
    /* margin-right: 1rem; */
  }
  .tips .close{
    float: right;
    margin-right: 2rem;
    color: #000000;
  }
  .uli li{
    width: 100%;
    height: 10rem;
    padding: 1.5rem 0;
  }
  .uli span{
    display: block;
    float: left;
    width: 7rem;
    height: 7rem;
    line-height: 7rem;

    text-align: center;
    font-size: 5rem;
    color: white;
    margin: 0rem 2rem;
    border-radius: 1rem;
  }
  .green{
    background-color: #2ea372;
  }
  .blue{
    background-color: #3556a3;
  }
  .uli p{
    height: 7rem;
    line-height: 7rem;
    font-size: 2.5rem;
  }
</style>



